var that;
export default {
	data() {
		return {
			TabCur: 1,
			scrollLeft: 0,
			list: [],
			tab: [
				{
					id: 1,
					title: '待审核'
				},
				{ id: 2, title: '提现通过' },
				{ id: 3, title: '驳回' }
			],
			page: 1,
			nolist: false,
			nomore: false,
			lodstatus: ''
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			that.page = 1;
			that.list = [];
			that.getlist();
		},
		getlist(i) {
			if (that.nomore) {
				return;
			}
			that.lodstatus = 'loading';
			that.nolist = false;
			this.$request
				.request({
					url: that.api.anchor_order.cash_log,
					data: {
						type: that.TabCur,
						page: that.page
					}
				})
				.then(res => {
					console.log(res);
					that.lodstatus = '';
					if (res.length > 0) {
						that.list = that.list.concat(res);
						that.page = that.page + 1;
						that.lodstatus = 'more';
					} else {
						if (that.page != 1) {
							that.nomore = true;
							that.lodstatus = 'noMore';
						} else {
							that.nolist = true;
						}
					}
				});
		}
	},
	onLoad() {
		that = this;
		that.getlist(1);
	},
	onReachBottom() {
		that.getlist();
	},
	onPullDownRefresh() {
		that.list = [];
		that.page = 1;
		that.getlist(1);
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	}
};
